html,
body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.root,
.app {
  height: 100%;
}

.app {
  background-color: #3181f2;
  max-width: 50%;
  margin: 0 auto;
}
.countdown {
  padding: 20px 20px 0 20px;
  background: linear-gradient(#368af4, #3181f2);
  color: #fff;
  font-size: 1.8rem;
}

.item-icon {
  margin-left: 10px;
  float: right;
}

.item-icon:hover {
  cursor: pointer;
}
.edit-event {
  padding: 10px;
  background-color: #f58f3c;
}

.edit-event__input-group {
  margin-top: 10px;
}

.edit-event__input-group__input {
  width: 80%;
  height: 20px;
  float: right;
  border: none;
}

.edit-event__input-group__button {
  margin-top: 10px;
  width: 100%;
  height: 30px;
  border: 2px solid #fff;
  background: none;
  color: #fff;
  cursor: pointer;
}

.edit-event__input-group__button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}
